<template>
  <v-layout align-center justify-center>
    <v-flex xs12 sm8 md6 lg5 xl4>
      <v-card>
        <v-toolbar dark color="black darken-3">
          <v-toolbar-title> Viaje finalizado</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <h1>{{ conductor }}</h1>
               <v-text-field
              label="Escribe como fue tu experiencia en el viaje"
            placeholder="Feedback"
            v-model="feedback"
          ></v-text-field>
          <v-flex class="red--text" v-if="errorM">
            {{ errorM }}
          </v-flex>
        </v-card-text>
        <v-card-actions class="px-3 pb-3">
          <v-flex text-xs-right>
            <v-btn @click="enviarFeedback()" >Enviar</v-btn>
          </v-flex>
       </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
  <!--   </v-app>  -->
</template>

<script>
import axios from "axios";

export default {
  name: "pasajeroViajeFinalizado",
  props: {},
  data() {
    return {
      conductor: "Viaje finalizado",
      theUser: "",
      feedback:""
    };
  },
  methods: {
    enviarFeedback: function () {
        var payload = { feedbackCliente: this.feedback, idHistorialRuta: this.$store.state.idHistorial, estadoViaje: "2"};
      console.log(payload);
      axios({
        url: "historialruta/pasajero/feedback",
        data: payload,
        method: "POST",
      })
        .then((resp) => {  
            console.log(resp.data);
      this.$router.push("/pasajerosolicitarviaje");

        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {},
};
</script>

<style scoped>
h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
