</template>

<script>
import axios from "axios";

export default {
  name: "pasajeroViaje",
  props: {},

  data() {
    return {
      conductor: "Esperando lista pasajeros...",
      theUser: "",
      pasajerosDisponibles: [],
      timer: null,
      recarga: 1,
    };
  },
  computed: {
    filteredStudents() {
      return this.uStudents.filter((user) => {
        const filter = this.filter.toUpperCase();
        const hasIdMatch = String(user.student_id).includes(filter);
        const hasNameMatch = user.student_name.toUpperCase().includes(filter);

        return hasIdMatch || hasNameMatch;
      });
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    aceptarViajeidConductor: function (varHistorialId) {
      console.log("Imprimiendo id usuario");
      console.log(this.$store.state.theUser);
      var payload = {
        idConductor: this.$store.state.theUser,
        historialId: varHistorialId,
      };
      axios({
        url: "/historialruta/conductoraceptaviaje",
        data: payload,
        method: "POST",
      })
        .then((resp) => {
          console.log(resp.data);
          this.$store.dispatch(
            "guardarIdHistorial",
            resp.data.historialListo._id
          );
          this.$router.push("/conductorenviaje");
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getUnassignedStudents() {
      axios
        .get(`/api/internship/unassignedStudents/${this.$route.params.id}`)
        .then((response) => (this.uStudents = response.data));
    },
    initialize() {
      this.pasajerosDisponibles = [];
      axios({
        url: "/historialruta/buscarpasajero",
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then((resp) => {
          console.log(resp.data.historial);
          this.pasajerosDisponibles = resp.data.historial;
          console.log("Imprimiendo" + this.pasajerosDisponibles);
        })
        .catch((err) => {
          console.log(err);
        });
    },

    viajeFinalizado: function () {
      this.$router.push("conductor/viajefinalizado");
    },
    cancelarViaje: function () {
      this.$router.push("conductor/viajecancelado");
    },
  },
  mounted() {
    this.timer = setInterval(() => {
      if (this.recarga == 1) {
        this.initialize();
      }
    }, 15000);
  },

  beforeDestroy() {
    clearInterval(this.timer);
  },
};
</script>

