<template>
  <v-layout align-center justify-center>
    <v-flex xs12 sm8 md6 lg5 xl4>
        <v-card>
          <v-toolbar dark color="black darken-3">
            <v-toolbar-title> Solicitar viaje </v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            
            <h1>Datos conductor</h1>
            <h2>Nombre conductor: {{ historialRefrescar.idConductor.nombre }}</h2>
            <h2>Teléfono: {{ historialRefrescar.idConductor.telefono }}</h2>
            <h2>Modelo vehículo: {{ historialRefrescar.idConductor.modeloVehiculo }}</h2>
            <h2>Placas: {{ historialRefrescar.idConductor.placaVehiculo }}</h2>
            <h2>ID conducción: {{ historialRefrescar.idConductor.idLicenciaConduccion }}</h2>
            
            <v-flex class="red--text" v-if="errorM">
              {{ errorM }}
            </v-flex>
          </v-card-text>
          <v-card-actions class="px-3 pb-3">
           
            <v-flex text-xs-right>
              <v-btn @click="cancelarViaje()" 
                >Cancelar viaje</v-btn
              >
            </v-flex>
            <v-flex text-xs-right>
              <v-btn @click="viajeFinalizado()" 
                >Viaje finalizado</v-btn
              >
            </v-flex>
          </v-card-actions>
        </v-card>
  
    </v-flex>
  </v-layout>
  <!--   </v-app>  -->
</template>

<script>
import axios from "axios";

export default {
  name: "pasajeroAceptadoConductor",
  props: {},
  data() {
    return {
      conductor: "Esperando conductor...",
      theUser: "",
      historialRefrescar: [],
      mostrar: 0,
      timer: null,
      recarga: 1,
    };
  },
  methods: {
    refrescar () {
      //console.log(this.$store.state.theUser);
      //var payload = { historialId: this.$store.state.idHistorial};
      console.log("imprimiendo historial "+ this.$store.state.idHistorial)
      axios({
        url: "historialruta/refrescarconductor/" + this.$store.state.idHistorial,
        method: "POST",
      
      })
        .then((resp) => {
         console.log(resp.data)
          this.historialRefrescar = resp.data.historial;
          
          console.log(this.historialRefrescar);
            if(this.historialRefrescar.estadoViaje == 2){
                this.$router.push("pasajero/viajefinalizado");
            }
            else if(this.historialRefrescar.estadoViaje == 3){
                this.$router.push("pasajero/viajecancelado");
            }
            
       
        })
        .catch((err) => {
          console.log(err);
        });
    },
    viajeFinalizado: function () {
      this.$router.push("pasajero/viajefinalizado");
    },
    cancelarViaje: function () {
      this.$router.push("pasajero/viajecancelado");
    },
  },
  mounted() {
    this.timer = setInterval(() => {

   this.refrescar()
 
    }, 4000);
  },

   beforeDestroy() {
    clearInterval(this.timer);
  }, 
};
</script>

<style scoped>
h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
