<template>

 <v-layout align-center justify-center>

        <v-flex xs12 sm8 md6 lg5 xl4>
            <v-card>
                <v-toolbar dark color="black darken-3">
                    <v-toolbar-title>
                        Registro
                    </v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                    <v-text-field v-model="nombre" autofocus color="accent" label="Nombre" required>
                    </v-text-field>
                    <v-text-field v-model="email" color="accent" label="Email" required>
                    </v-text-field>
                    <v-text-field v-model="password" type="password" color="accent" label="Password" required>
                    </v-text-field>
                     <v-text-field v-model="telefono" type="number" color="accent" label="Teléfono" required>
                    </v-text-field>
                     <v-text-field v-model="numeroDocumentoIdentidad"  color="accent" label="Número documento" required>
                    </v-text-field>
                     <v-text-field v-model="idLicenciaConduccion"  color="accent" label="ID licencia conducción" required>
                    </v-text-field>
                     <v-text-field v-model="soat"  color="accent" label="SOAT" required>
                    </v-text-field>
                     <v-text-field v-model="modeloVehiculo"  color="accent" label="Modelo vehículo" required>
                    </v-text-field>
                     <v-text-field v-model="placaVehiculo"  color="accent" label="Placa vehículo" required>
                    </v-text-field>
                     <v-text-field v-model="colorVehiculo"  color="accent" label="Color vehículo" required>
                    </v-text-field>
                    <v-flex class="red--text" v-if="errorM">
                        {{errorM}}
                    </v-flex>
                </v-card-text>
                <v-card-actions class="px-3 pb-3">
                    <v-flex text-xs-right>
                        <v-btn @click="registro()" >Registrarme</v-btn>
                    </v-flex>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
<!--   </v-app>  -->
</template>

<script>
import axios from "axios"

export default {
  name: "Registro",
  props: {},
  data() {
    return {
      nombre: "",
      email:"",
      password: "",
      telefono: "",
      numeroDocumentoIdentidad: "",
      idLicenciaConduccion: "",
      soat: "",
      modeloVehiculo: "",
      placaVehiculo: "",
      colorVehiculo: ""
    };
  },
  methods: {
    registro: function () {
      ///
      var payload = {nombre : this.nombre, correo: this.email, contrasena : this.password, telefono : this.telefono, numeroDocumentoIdentidad : this.numeroDocumentoIdentidad, idLicenciaConduccion : this.idLicenciaConduccion, soat : this.soat, modeloVehiculo : this.modeloVehiculo, placaVehiculo : this.placaVehiculo, colorVehiculo : this.colorVehiculo};
      axios({
        url: "usuarioaplicacion/registro", //http://localhost:5000/api/
        data: payload,
        method: "POST",
      })
        .then(() => {
    
            this.$router.push("/login");
            // console.log(this.$router.currentRoute.fullPath);
          
        })
        .catch((err) => {
          console.log(err);
      });

      ///
    },
  },

};
</script>

<style scoped>

h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
