<template>
  <v-layout align-center justify-center>
    <v-flex xs12 sm8 md6 lg5 xl4>
      <v-card>
        <v-toolbar dark color="black darken-3">
          <v-toolbar-title> Datos pasajero </v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <h1>{{ conductor }}</h1>
          <h2>Nombre: {{ datosUsuario.idPasajero.nombre }}</h2>
          <h2>Correo: {{ datosUsuario.idPasajero.correo }}</h2>
          <h2>Teléfono: {{ datosUsuario.idPasajero.telefono }}</h2>
          <h2>Documento identidad: {{ datosUsuario.idPasajero.numeroDocumentoIdentidad }}</h2>
 
          <v-flex class="red--text" v-if="errorM">
            {{ errorM }}
          </v-flex>
        </v-card-text>
        <v-card-actions class="px-3 pb-3">
        
          <v-flex text-xs-right>
            <v-btn @click="cancelarViaje()" 
              >Cancelar viaje</v-btn
            >
          </v-flex>
          <v-flex text-xs-right>
            <v-btn @click="viajeFinalizado()" 
              >Viaje finalizado</v-btn
            >
          </v-flex>
        </v-card-actions>
      </v-card>
      <!--  -->
    </v-flex>
  </v-layout>
  <!--   </v-app>  -->
</template>

<script>
import axios from "axios";

export default {
  name: "pasajeroViaje",
  props: {},
  data() {
    return {
      conductor: "Viaje aceptado, apresúrate",
      theUser: "",
      datosUsuario: [],
      timer: null,
      recarga : 1
    };
  },
  methods: {
    refrescar: function () {
      var payload = { historialId: this.$store.state.idHistorial };
      axios({
        url: "historialruta/refrescarpasajero",
        data: payload,
        method: "POST",
      })
        .then((resp) => {
          console.log(resp.data);
          this.datosUsuario = resp.data.historial;
          this.$store.dispatch("guardarIdHistorial", resp.data.historial._id);
          /* if(this.datosUsuario){this.recarga = 0}
          else{this.recarga =1} */
          if(this.datosUsuario.estadoViaje == 2){
            this.$router.push("conductor/viajefinalizado");
          }
          else if(this.datosUsuario.estadoViaje == 3){
            this.$router.push("conductor/viajecancelado");
          }
})
        .catch((err) => {
          console.log(err);
        });
    },
    viajeFinalizado: function () {
      this.$router.push("conductor/viajefinalizado");
    },
    cancelarViaje: function () {
      this.$router.push("conductor/viajecancelado");
    },
  },
  mounted() {
    this.timer = setInterval(() => {
      if (this.recarga == 1) {
        this.refrescar();
      }
    }, 4000);
  },

   beforeDestroy() {
    clearInterval(this.timer);
  }, 
};
</script>

<style scoped>
h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
