<template>
  <v-layout align-center justify-center>
    <v-flex xs12 sm10 md10 lg7 xl4>
      <v-card>
        <v-simple-table>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">Nombre</th>
                <th class="text-left">Direccion origen</th>
                <th class="text-left">Direccion destino</th>
                <th class="text-left">Dinero ofertado</th>
              
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in historial" :key="item.name">
                <td>{{ item.idPasajero.nombre }}</td>
                <td>{{ item.lugarOrigen }}</td>
                <td>{{ item.lugarDestino }}</td>
                <td>{{ item.dineroPagado }}</td>
             
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card>

      <!--  -->
    </v-flex>
  </v-layout>
  <!--   </v-app>  -->
</template>

<script>
import axios from "axios";

export default {
  name: "historialPasajero",
  props: {},
  data() {
    return {
      theUser: "",
      historial: [],
    };
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      //console.log(this.$store.state.theUser);
      var payload = { idPasajero: this.$store.state.theUser };
      axios({
        url: "/historialruta/historialpasajero",
        method: "POST",
        data: payload,
      })
        .then((resp) => {
          console.log(resp.data);
          this.historial = resp.data.historial;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style scoped>
h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
